.effect-cards {
  position: relative;
  border: 1px solid oklch(0.985 0.002 247.839 / 0.58);
}
@keyframes pulsePlay {
  0% {
    transform: translate(-50%, -50%) scale(1);
    box-shadow: 0 0 0 0 oklch(0.43 0.075 195.74 / 0.7);
  }
  50% {
    transform: translate(-50%, -50%) scale(1.15);
    box-shadow: 0 0 0 8px oklch(0.43 0.075 195.74 / 0);
  }
  100% {
    transform: translate(-50%, -50%) scale(1);
    box-shadow: 0 0 0 0 oklch(0.43 0.075 195.74 / 0.7);
  }
}
.effect-cards img {
  display: block;
}
.effect-cards .overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 1;
  pointer-events: none;
}
.effect-cards:hover .overlay {
  opacity: 1;
}
.effect-cards .play {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 2;
  pointer-events: none;
  background-color: oklch(0.43 0.075 195.74);
  padding: 10px;
  border-radius: 100%;
}
.effect-cards .heart {
  position: absolute;
  top: 12px;
  left: 8px;
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 2;
  pointer-events: none;
}
.effect-cards:hover .play {
  opacity: 1;
  animation: pulsePlay 2s ease-in-out infinite;
}
.effect-cards:hover .heart {
  opacity: 1;
}
.effect-cards .play > *,
.effect-cards .heart > * {
  display: block;
  pointer-events: auto;
}
.effect-cards .play > * {
  font-size: 2.25rem;
}
.effect-cards .heart > * {
  font-size: 1.5rem;
}
